
import BScroll from 'better-scroll'
import XTable from 'vux/src/components/x-table/index.vue'

import {getEnrollPlan} from '@/api/index'
export default {
components: { XTable },
data () {
return {
enrollPlanList: [], // 招生计划
enrollScroll: null,
}
},
computed: {
schoolName () {
return this.$route.query.schoolName
},
openid () {
return this.$store.state.user.openid
},
},
mounted () {
this.getEnrollPlan()
this.init()
},
methods: {
returnBack () {
this.$router.go(-1)

},
init () {
this.$nextTick(() => {
this.enrollScroll = new BScroll(this.$refs.enroll_plan_second, {
click: true
})
})
},
getEnrollPlan () { // 获取招生详情
getEnrollPlan({
openid: this.openid,
schoolname: this.schoolName,
year: '2019'
}).then(res => {
this.enrollPlanList = res.data.data
console.log('妆业招生详情', res.data)
})
},
gotoTdInfo (index) {
console.log('index', index)
var a = document.querySelectorAll('.ttd')
a[index].className = 'ttd td_info_no'
console.log(a[index].className)
}
}
}



